import React, { useEffect, useState } from 'react';
import Head from 'next/head';
import Link from 'next/link';
import { useRouter } from 'next/router';
import Router from 'next/router';
import axios from 'axios';
import ReactDisqusComments from 'react-disqus-comments';
import Gist from 'react-gist';
import AdSense from 'react-adsense';

import { baseUrl } from '../../constants/constants';
import NavBar from '../../components/NavBar';
import Share from '../../components/Share';
import { getDate, printDate, getRecentArticles } from '../../services/utils';
import RecentArticles from '../../components/RecentArticles';

export default function Blog(props) {
    const { query } = useRouter();
    const { id } = query;
    const [blog, setBlog] = useState({
        title: '',
        description: '',
        categoryId: '',
        readTimeMin: 0,
        date: getDate(),
        content: [],
    });
    const [recent, setRecent] = useState([]);

    useEffect(() => {
        if (!id)
            return;
        axios.get(`${baseUrl}/api/articles/${id}.json`)
            .then(res => {
                if (res) {
                    const ress = res;
                    setBlog({
                        content: res.data.content && res.data.content.length ? res.data.content : [],
                        title: res.data.title,
                        description: res.data.description,
                        date: getDate(res.data.date),
                        readTimeMin: res.data.readTimeMin,
                        categoryId: res.data.categoryId,
                    });
                }
                window.scrollTo(0, 0);
            })
            .catch(err => {
                Router.push('/', '/', { shallow: false });
            });
    }, [id]);

    useEffect(() => {
        getRecentArticles()
            .then((res) => {
                if (res)
                    setRecent(res.data && res.data.length ? res.data : []);
            })
            .catch(err => {
            });
    }, []);

    const handleNewComment = () => { };

    const renderBlog = content => {

        if (!content || content.length <= 0)
            return null;

        const blog = content.map((line, index) => {
            if (line.isGist && line.gist)
                return <Gist key={index} className="mt-3 mb-3" id={line.gist} />;

            if (line.isMainHeading)
                return <div className="main-heading" key={index} dangerouslySetInnerHTML={{ __html: line.html }}></div>;

            if (line.isSubHeading)
                return <div className="sub-heading" key={index} dangerouslySetInnerHTML={{ __html: line.html }}></div>;

            if (line.isCodeSection)
                return <div className="code-section" key={index} dangerouslySetInnerHTML={{ __html: line.html }}></div>;

            if (line.isQuoted)
                return <div className="quote" key={index} dangerouslySetInnerHTML={{ __html: line.html }}></div>;

            return <div key={index} dangerouslySetInnerHTML={{ __html: line.html }}></div>;
        });
        return blog;
    };

    const printBlog = renderBlog(blog.content);
    const shareUrls = {
        linkedIn: `https://www.linkedin.com/shareArticle?mini=true&url=https%3A//www.dineshmg.com/${id}&title=Learn%20Web%20development&summary=&source=`,
        facebook: `https://www.facebook.com/sharer/sharer.php?u=https%3A//www.dineshmg.com/${id}`,
        twitter: `https://twitter.com/home?status=https%3A//www.dineshmg.com/${id}`,
    };

    return (<>
        <NavBar />
        <Head>
            <title>{blog.title}</title>
            <meta name="description" content={blog.description} />
        </Head>
        <span className="blog-page">
            <div className="blog-page__container">
                <Share urls={shareUrls} />
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <AdSense.Google
                                client='ca-pub-3929370842605036'
                                slot='3072497734'
                                format='auto'
                                responsive='true'
                            />
                        </div>
                        <div className="col-12">
                            <div className="heading-text">
                                {blog.title}
                            </div>
                            <p className="lead">
                                {printDate(blog.date)}
                                &nbsp;-&nbsp;{blog.readTimeMin} mins read
                                <ul className="share">
                                    <li><a target="_blank" className="mr-5" rel="noopener noreferrer" href={shareUrls.linkedIn}><i className="fab fa-linkedin"></i></a></li>
                                    <li><a target="_blank" className="mr-5" rel="noopener noreferrer" href={shareUrls.facebook}><i className="fab fa-facebook"></i></a></li>
                                    <li><a target="_blank" className="mr-5" rel="noopener noreferrer" href={shareUrls.twitter}><i className="fab fa-twitter"></i></a></li>
                                </ul>
                            </p>
                        </div>
                        <div className="contents col-12">
                            {printBlog}
                        </div>
                        <div className="col-12">
                            <hr></hr>
                        </div>
                        <div className="col-12">
                            <div className="recent">
                                Recent Articles:
                            </div>
                        </div>
                        <RecentArticles articles={recent} />
                        <div className="col-12">
                            <hr></hr>
                        </div>
                        <div className="col-12">
                            <ReactDisqusComments
                                shortname="shadowfalls-github-io-webtutor-1"
                                identifier={blog.title + '123'}
                                title={blog.title}
                                onNewComment={handleNewComment} />
                        </div>
                    </div>
                </div>
            </div>
        </span>
    </>);
}

export async function getInitialProps({ Component, ctx, pageProps }) {
    console.log(ctx);
    console.log(pageProps);
    console.log('-----------------------------------------------------------------------');
    return <></>
    // return <Component {...pageProps} />
}
